function initBtnList() {
    const width = 100
    const height = 50
    const padding = 20
    const { windowHeight, windowWidth } = wxGameSdk.getSystemInfo()
    const max = Math.floor(windowWidth / width)
    const space = (windowWidth - padding - max * width) / max
    let top = 100
    let col = 0

    btnList.forEach(({ label, onClick }, index) => {
        const _index = index % max
        let left = (width + space) * _index + padding
        if (index && _index === 0) {
            left = padding
            col += 1
            top += height + space
        }

        const button = wx.createUserInfoButton({
            type: 'text', // ÊñáÊú¨ÊåâÈíÆ
            text: label, // ÊåâÈíÆÊñáÂ≠ó
            style: {
                left,
                top,
                width, // ÊåâÈíÆÂÆΩÂ∫¶
                height, // ÊåâÈíÆÈ´òÂ∫¶
                backgroundColor: '#ff5722', // ÊåâÈíÆËÉåÊôØËâ≤
                color: '#ffffff', // ÊåâÈíÆÊñáÂ≠óÈ¢úËâ≤
                fontSize: 16, // ÊåâÈíÆÊñáÂ≠óÂ§ßÂ∞è
                borderRadius: 8, // ÊåâÈíÆÂúÜËßí
                textAlign: 'center', // ÊåâÈíÆÊñáÂ≠óÂØπÈΩêÊñπÂºè
                lineHeight: 50, // ÊåâÈíÆÊñáÂ≠óË°åÈ´ò
            },
        })
        button.onTap(onClick)
    })
}
initBtnList()
